VARIABLES
---------------------------*/
/*---------------------------
IMPORTS
---------------------------*/
.header {
  padding: 20rem 0;
  text-align: center;
}
.header__title {
  display: inline-block;
  position: relative;
  margin-bottom: 3rem;
  padding: 0 7rem;
  font-family: "Princess Sofia", cursive;
  font-size: 7rem;
}
.header__title::before {
  content: "";
  position: absolute;
  bottom: 3rem;
  left: 50%;
  width: 100%;
  height: 3px;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.3);
}
.header__subtitle {
  display: block;
  font-size: 2.2rem;
  text-transform: uppercase;
  letter-spacing: 0.8rem;
  font-weight: 400;
}
.header span {
  font-weight: 600;
}

/*---------------------------
MAIN
---------------------------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: "Nunito", sans-serif;
}

body {
  height: 100vh;
  width: 100%;
  background-color: #e7dfd3;
}

body::after {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  content: "";
  height: 100vh;
  width: 100%;
  background-color: #e7dfd3;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  clip-path: polygon(0% 10%, 100% 10%, 100% 70%, 0% 70%);
